// Breadcrumb component styles
.breadcrumb {

  // Shared CSS custom properties
  --#{$prefix}breadcrumb-divider: #{escape-svg($breadcrumb-divider)};
  --#{$prefix}breadcrumb-expander: #{escape-svg($breadcrumb-expander)};
  --#{$prefix}breadcrumb-item-active-color: var(--#{$prefix}dark-grey-muted);
  --#{$prefix}breadcrumb-divider-color: var(--#{$prefix}light-action-secondary);
  --#{$prefix}breadcrumb-expander-color: var(--#{$prefix}light-action-secondary);
  --#{$prefix}breadcrumb-item-padding-x: 0;
  --#{$prefix}breadcrumb-bg: transparent;
  --#{$prefix}breadcrumb-margin-bottom: 0;
  --#{$prefix}breadcrumb-padding-y: 2rem;
  --#{$prefix}breadcrumb-link-color: var(--#{$prefix}light-link);
  --#{$prefix}breadcrumb-link-text-decoration-color: var(--#{$prefix}light-link);
  --#{$prefix}breadcrumb-link-text-decoration-color-hover: var(--#{$prefix}light-link);

  // Dark mode overrides
  .dark &,
  .dark-alt & {
    --#{$prefix}breadcrumb-item-active-color: var(--#{$prefix}color-default-color-dark-text-lighter);
    --#{$prefix}breadcrumb-divider-color: var(--#{$prefix}dark-action-secondary);
    --#{$prefix}breadcrumb-link-color: var(--#{$prefix}dark-link);
    --#{$prefix}breadcrumb-link-text-decoration-color: var(--#{$prefix}dark-link);
    --#{$prefix}breadcrumb-link-text-decoration-color-hover: var(--#{$prefix}dark-link);
  }

  &.expanded {
    .breadcrumb-toggle {
      display: none;
    }
  }
}

// Breadcrumb items
.breadcrumb-item {
  font-size: 0.875rem;
  line-height: 1.25rem;
  display: none;

  // Override Bootstrap's default :before pseudo element
  &:before {
    display: none;
  }

  // Divider
  &:after {
    content: "";
    display: block;
    width: 1.5rem;
    mask-image: var(--#{$prefix}breadcrumb-divider);
    mask-size: 1rem 1rem;
    mask-repeat: no-repeat;
    mask-position: center center;
    background-color: var(--#{$prefix}breadcrumb-divider-color);
  }

  &:last-child:after {
    display: none;
  }

  // Breadcrumb link
  a,
  a:visited,
  a:hover {
    color: var(--#{$prefix}breadcrumb-link-color);
    text-decoration-color: var(--#{$prefix}breadcrumb-link-text-decoration-color);
  }

  &.shortened {
    height: 0;
    overflow: hidden;
    max-width: 0;
    transition: max-width 0s;
    padding: 0;
  }

  &.breadcrumb-toggle {
    border: 0;
    padding: 0;
    vertical-align: inherit;

    a {
      display: inline-block;
      width: 32px;
      line-height: 1.5rem;
      mask-image: var(--#{$prefix}breadcrumb-expander);
      background-color: var(--#{$prefix}breadcrumb-link-color);
      mask-size: 2rem 2rem;
      mask-repeat: no-repeat;
      mask-position: center center;
    }
  }

}

// Responsive styles
@include media-breakpoint-down(lg) {

  .breadcrumb {
    --#{$prefix}breadcrumb-padding-y: 1rem;
  }

  .breadcrumb-item {
    &:nth-last-child(2) {
      display: flex;
      flex-direction: row-reverse;

      // Rotate the back arrow 180 degrees, shift left to align chevron with text 
      &:after {
        transform: rotate(180deg);
        transform-origin: center;
        margin-left: -0.25rem;
      }

    }
  }
}

//Large and above
@include media-breakpoint-up(lg) {
  .breadcrumb-item {
    display: flex;
    font-size: 1rem;
    line-height: 1.5rem;
  }
}